button {
    width: 55px;
    height: 55px;
    border: none;
    outline: none;
    margin: 3px;
    cursor: pointer;
    transition: all .3s;
}
button:hover{
    opacity: .5;
}

.section {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #e34354;
}

.calc {
    display: grid;
    grid-template-columns: 12fr;

}

.header {
    display: flex;
    justify-content: center;
    padding: 20px;
    background-color: #ecedf4;

}
.header input{
    width: 100%;
    height: 65px;
    background-color: #02354F;
    color: #ffffff;
    font-size: 19px;
    outline: none;
    text-align: right;
    padding-right: 5px;

}




.hero {
    display: grid;
    grid-template-columns: 12fr;
    background-color: #fff;
    padding: 20px;
}

.numbers {
    display: grid;
    grid-template-columns: 12fr;
}


.operations button{
    background-color: #A2A39D;
}

.divide{
    background-color: #E0683C;
}